<template lang="html">
<div class="loginPanel">
	<div class="login" v-show="mode === 'login'">
		<div class="method-title"></div>
		<div class="google-bt" v-on:click="LoginByGoogle();">Google </div>
		<div class="facebook-bt" v-on:click="LoginByFacebook();">Facebook </div>
		<div class="seperator"></div>
		<div class="method-title"></div>
		<form ref="loginForm" method="post" v-bind:action="pwLoginAction">
			<div class="relative-box">
				<div class="item-label"></div>
				<div class="forget-password" v-on:click="ForgetPassword();"></div>
			</div>
			<input type="email" name="email" v-model="email" placeholder="">
			<div class="item-label"></div>
			<input type="password" name="password" v-model="password" placeholder="" v-on:keyup.13="LoginByPassword();">
			<div class="login-bt" v-on:click="LoginByPassword();"></div>
		</form>
		<div class="text-link" v-on:click="mode='signup';"></div>
	</div>
	
	<div class="signup" v-show="mode === 'signup'">
		<div class="method-title"></div>
		<div class="google-bt" v-on:click="LoginByGoogle();">Google </div>
		<div class="facebook-bt" v-on:click="LoginByFacebook();">Facebook </div>
		<div class="seperator"></div>
		<form ref="signupForm" method="post" v-bind:action="pwSignupAction">
			<div class="method-title"></div>
			<div class="item-label"></div>
			<input type="email" name="email" v-model="email" placeholder="">
			<div class="item-label"></div>
			<input type="text" name="name" v-model="name" placeholder="">
			<div class="item-label"></div>
			<input type="password" name="password" v-model="password" placeholder="">
			<div class="item-label"></div>
			<input type="password" v-model="passwordConfirm" placeholder="" v-on:keyup.13="SignupByPassword();">
			<div class="login-bt" v-on:click="SignupByPassword();"></div>
		</form>
		<div class="text-link" v-on:click="mode='login';"></div>
	</div>

	<div class="resetPassword" v-show="mode === 'resetPassword'">
		<form ref="resetForm" method="post" action="/auth/reset-password">
			<div class="item-label"></div>
			<input type="password" name="password" v-model="password" placeholder="">
			<div class="item-label"></div>
			<input type="password" v-model="passwordConfirm" placeholder="" v-on:keyup.13="ResetPassword();">
			<div class="login-bt" v-on:click="ResetPassword();"></div>
		</form>
	</div>
</div>
</template>

<script>
var g_Util = require('../js/util');

export default {
	data: function () {
		return {
			intentUrl: "",
			email: "",
			name: "",
			password: "",
			passwordConfirm: "",
			token: "",
			mode: "login",
			pwLoginAction: "/auth/login-by/password",
			pwSignupAction: "/auth/signup-by/password"
		};
	},
	created: function(){
		var urlParam = g_Util.GetUrlParameter();
		if(urlParam.intentUrl){
			this.intentUrl = encodeURIComponent(urlParam.intentUrl);
		}

		if(urlParam.reset == 1){
			this.mode = "resetPassword";
			this.token = urlParam.token;
		}
	},
	methods: {
		ValidateEmail: function(email){
			var pattern = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
			return pattern.test(email);
		},
		LoginByGoogle: function(){
			var str = "/auth/login-by-google";
			if(this.intentUrl) str += "?intentUrl="+this.intentUrl;
			window.location.href = str;
		},
		LoginByFacebook: function(){
			var str = "/auth/login-by-facebook";
			if(this.intentUrl) str += "?intentUrl="+this.intentUrl;
			window.location.href = str;
		},
		LoginByPassword: function(){
			if(this.email == "") return alert("");
			else if(!this.ValidateEmail(this.email))  return alert("");
			else if(this.password == "") return alert("");
			if(this.intentUrl) this.pwLoginAction = "/auth/login-by-password?intentUrl="+this.intentUrl;
			this.$refs.loginForm.submit();
		},
		SignupByPassword: function(){
			if(this.email == "") return alert("");
			else if(!this.ValidateEmail(this.email))  return alert("");
			else if(this.password == "") return alert("");
			else if(this.name == "") return alert("");
			else if(this.password != this.passwordConfirm) return alert("");
			if(this.intentUrl) this.pwSignupAction = "/auth/signup-by-password?intentUrl="+this.intentUrl;
			this.$refs.signupForm.submit();
		},
		ForgetPassword: function(){
			if(this.email == "") return alert("");
			else if(!this.ValidateEmail(this.email))  return alert("");

			$.post("/auth/forget-password", {email: this.email}, function(data){
				console.log(data);
				if(data.status == "ok"){
					alert("");
				}
			});
		},
		ResetPassword: function(){
			if(this.password == "") return alert("");
			else if(this.password != this.passwordConfirm) return alert("");

			$.post("/auth/reset-password", {password: this.password, token: this.token}, function(data){
				console.log(data);
				if(data.status == "ok"){
					if(data.status == "ok"){
						alert("");
						window.location.href="/auth/login";
					}
				}
			});
		}
	}
}
</script>

<style lang="scss" scoped>
@import "../scss/main.scss";

.loginPanel{
	width: 300px;
	max-width: 100%;
	margin: auto;
	@mixin bt{
		padding: 10px;
		margin: 10px 0px;
		text-align: center;
		cursor: pointer;
		border-radius: 5px;
	}
	.google-bt{
		@include bt;
		color: white;
		background-color: rgb(220,78,65);
	}
	.facebook-bt{
		@include bt;
		color: white;
		background-color: rgb(59,87,157);
	}
	.seperator{
		padding: 10px;
		margin-bottom: 10px;
		border-bottom: 1px solid #dddddd;
	}
	.method-title{
		text-align: center;
		font-size: 1.2em;
	}
	.relative-box{
		position: relative;
	}
	.forget-password{
		position: absolute;
		top: 0px;
		right: 0px;
		font-size: 0.8em;
		color: #666666;
		cursor: pointer;
	}
	.item-label{
		
	}
	input{
		padding: 5px;
		width: 100%;
	}
	.login-bt{
		@include bt;
		color: white;
		background-color: rgb(100,100,100);
	}
	.text-link{
		text-align: center;
		cursor: pointer;
	}
}
</style>